$lightbrown: #743e28;
$darkbrown: #bb8c5d;
$lightgray: #AAA7A6;

body {
  background: white;
}

.container {
  position: absolute;
  overflow: hidden;
  height: auto;
  width: 100%;
  top: 0px;
  left: 0px;
  z-index: 2500;
}

.container-center {
  position: relative;
  height: 100vh;
  width: 80%;
  margin:0 auto;
  // background-color: green;
}

.left{
  position: absolute;
  background: url(../dist/assets/pawprint1.png);
  background-size: 100px;
  background-repeat: space;
  height: 100vh;
  width: 10%;
  top: 50px;
}

.right{
  position: absolute;
  background: url(../dist/assets/pawprint2.png);
  background-size: 100px;
  background-repeat: space;
  height: 100vh;
  width: 10%;
  top: 50px;
  right: 0px;
}

nav {
  height: 50px;
  background-color: $lightgray;
  overflow: hidden;
}

.featured-container{
  height: 200px;
  width: 100%;
  // background-color: blue;
}

#btn:hover {
  cursor: pointer;
}

